import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { IData, data$, data } from '../mock/data';

@Component({
  selector: 'app-info',
  templateUrl: './info.component.html',
  styleUrls: ['./info.component.scss']
})
export class InfoComponent implements OnInit {
  @Input() public items: IData[];
  @Output() public sendType = new EventEmitter<string>();
  @Output() public selectedItem = new EventEmitter<IData>();

  public data$ = data$;
  public data = data;

  constructor() { }

  ngOnInit() {
    this.selectFirstItem();
  }

  public onMenuClick(menuItemType) {
    if (menuItemType) {
      const firstReceivedItem = this.data.filter(item => {
        return item.type === menuItemType;
      });
      this.sendType.emit(menuItemType);
      this.selectedItem.emit(firstReceivedItem[0]);
    } else {
      this.sendType.emit('');
    }
  }

  public selectItem(item) {
    this.selectedItem.emit(item);
  }

  public selectFirstItem() {
    this.selectedItem.emit(this.items[0]);
  }

}
